function showTotal(total) {
  return ` ${total} `;
}
export function paginationConfig(total, onChange, onShowSizeChange) {
  return {
    size: 'large',
    total,
    showSizeChanger: true,
    defaultPageSize: 5,
    showQuickJumper: { goButton: '' },
    showTotal: showTotal,
    pageSizeOptions: ['5', '10', '20', '30'],
    onShowSizeChange,
    onChange
  }
}