import React, { Component } from 'react';
import { Table, Divider, Button, Modal, Form, Input, Switch, message, Tree } from 'antd';
import { addNewRole, roleList, updateOneRole, deleteOneRole, getMenuTreeList } from '@/api/login';
import { paginationConfig } from '@/config/paginationConfig';
import { getDate_0 } from '@/utils/tools';
import './index.less';

const { confirm } = Modal;
const { TreeNode } = Tree;

@Form.create()
class Role extends Component {
  state = {
    pageNum: 1,
    pageSize: 5,
    visible: false,
    confirmLoading: false,
    data: [],
    tree_data: [],
    _id: '',
    total: 0,
    type: true, // true  false 

    name: '', // 
    status: true, // false  true 

    expandedKeys: [], //  ---- key 
    autoExpandParent: true, // 
    checkedKeys: [], //  key key checkablecheckStrictlycheckedhalfChecked
    selectedKeys: [], //   
  }
  columns = [
    { title: '', dataIndex: 'name', key: 'name' },
    { title: '', dataIndex: 'createdAt', key: 'createdAt' },
    { title: '', dataIndex: 'if_status', key: 'if_status' },
    { title: '', dataIndex: 'updatedAt', key: 'updatedAt' },
    {
      title: '',
      dataIndex: '',
      key: 'x',
      render: (text, record) =>
        <span>
          <Button
            type="primary"
            onClick={() => this.handleUpdateData(record)}
          >
            
            </Button>
          <Divider type="vertical" />
          <Button
            type="danger"
            onClick={() => this.handleDeleteData(record)}
          >
            
            </Button>
        </span>
      ,
    },
  ]

  onExpand = expandedKeys => {
    this.setState({
      expandedKeys,
      autoExpandParent: false,
    });
  };

  onCheck = checkedKeys => {
    this.setState({ checkedKeys });
  };

  onSelect = (selectedKeys, info) => {
    this.setState({ selectedKeys });
  };

  renderTreeNodes = data =>
    data.map(item => {
      if (item.children) {
        return (
          <TreeNode title={item.title} key={item.key} dataRef={item}>
            {this.renderTreeNodes(item.children)}
          </TreeNode>
        );
      }
      return <TreeNode {...item} />;
    });
  componentWillMount() {
    this.load_role_list();
    this.get_menu_tree();
  }
  //  
  load_role_list() {
    let data = {
      pageNum: this.state.pageNum,
      pageSize: this.state.pageSize
    }
    roleList(data).then(res => {
      res.data.map((item, index) => {
        item.createdAt = getDate_0(item.createdAt, 'year');
        item.updatedAt = getDate_0(item.updatedAt, 'year');
        item.if_status = item.status ? '' : '';
        return item.key = index;
      })
      this.setState({
        data: res.data,
        total: res.total
      });
    });
  }
  //  modal
  showModal = () => {
    this.setState({
      visible: true,
      type: true
    });
  };
  //  menu tree
  get_menu_tree() {
    getMenuTreeList().then(res => {
      let tree_data = this.be_tree(res);
      this.setState({
        tree_data
      });
    });
  }
  be_tree(data) {
    data.map(item => {
      item.title = item.name;
      item.key = item._id;
      if (item.children && item.children.length) {
        this.be_tree(item.children);
      }
      return true;
    });
    return data
  }

  // 
  handleUpdateData(record) {
    // 
    const { status, name, checkedKeys, _id } = record;
    const { setFieldsValue } = this.props.form;
    this.setState({
      visible: true,
      type: false,
      status,
      checkedKeys,
      _id
    });

    setFieldsValue({
      name,
    });
  }
  //   
  handleDeleteData(record) {
    // 
    confirm({
      title: '',
      content: '',
      okText: '',
      okType: '',
      cancelText: '',
      onOk: () => {
        deleteOneRole({ _id: record._id }).then(res => {
          if (res.ok === 1) {
            message.success('');
            this.load_role_list();
          } else {
            message.error('');
          }
        });
      },
      onCancel() {
        console.log('Cancel');
      },
    });
  }
  onChange(pageNum, pageSize) {
    this.setState({
      pageNum,
      pageSize
    }, () => {
      this.load_role_list();
    });
  }

  onShowSizeChange(pageNum, pageSize) {
    this.setState({
      pageNum,
      pageSize
    }, () => {
      this.load_role_list();
    });
  }
  //  
  handleOk = (e) => {
    e.preventDefault();
    const { validateFields } = this.props.form;
    const { status, checkedKeys } = this.state;
    validateFields((err, values) => {
      if (!err) {
        const data = { ...values, status, checkedKeys };

        const { type } = this.state;
        if (type) {
          this.newAddRole(data);
        } else {
          this.updateRole(data);
        }
      }
    });
  };
  newAddRole(data) {
    const { setFieldsValue } = this.props.form;
    addNewRole(data).then(res => {
      if (res.code === 200) {
        message.success('');
        this.setState({
          confirmLoading: true,
        });
        setTimeout(() => {
          this.setState({
            visible: false,
            confirmLoading: false,
            status: true,
            checkedKeys: []
          });
          this.load_role_list();
          setFieldsValue({
            name: '',
          });
        }, 2000);
      } else {
        message.error('');
      }
    });
  }
  updateRole(data) {
    const { setFieldsValue } = this.props.form;
    data._id = this.state._id;
    updateOneRole(data).then(res => {
      if (res.code === 200) {
        message.success('');
        this.setState({
          confirmLoading: true,
        });
        setTimeout(() => {
          this.setState({
            visible: false,
            confirmLoading: false,
            status: true,
            checkedKeys: []
          });
          this.load_role_list();
          setFieldsValue({
            name: '',
          });
        }, 2000);
      } else {
        message.error('');
      }
    });
  }
  handleCancel = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({
      visible: false,
      confirmLoading: false,
      status: true,
      checkedKeys: []
    });
    setFieldsValue({
      name: '',
    });
  };
  handleChangeName = (e) => {
    this.setState({
      name: e.target.value
    });
  }
  handleChangeStatus = (status) => {
    this.setState({
      status
    })
  }
  render() {
    const { visible, confirmLoading, name, status, type, tree_data } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: { span: 4 },
      wrapperCol: { span: 18 },
    };
    return (
      <div className='content user-content'>
        <div className='user-content-header'>
          <Button type='primary' size='large' onClick={this.showModal}></Button>
        </div>
        <Table
          columns={this.columns}
          dataSource={this.state.data}
          pagination={paginationConfig(this.state.total, this.onChange.bind(this), this.onShowSizeChange.bind(this))}
        />
        <div>
          <Modal
            title={type ? '' : ''}
            visible={visible}
            onOk={this.handleOk}
            confirmLoading={confirmLoading}
            onCancel={this.handleCancel}
            cancelText=''
            okText=''
          >
            <Form {...formItemLayout}>
              <Form.Item label="">
                {getFieldDecorator('name', {
                  rules: [{ required: true, message: '!' }],
                  initialValue: name
                })(
                  <Input placeholder="" size='large' onChange={this.handleChangeName} />,
                )}
              </Form.Item>
              <Form.Item label="">
                <Switch checked={status} onChange={this.handleChangeStatus.bind(this)}/>
              </Form.Item>
              <Form.Item label=''>
                <Tree
                  checkable
                  onExpand={this.onExpand}
                  expandedKeys={this.state.expandedKeys}
                  autoExpandParent={this.state.autoExpandParent}
                  onCheck={this.onCheck}
                  checkedKeys={this.state.checkedKeys}
                  onSelect={this.onSelect}
                  selectedKeys={this.state.selectedKeys}
                >
                  {this.renderTreeNodes(tree_data)}
                </Tree>
              </Form.Item>
            </Form>
          </Modal>
        </div>
      </div>
    )
  }
}
export default Role;


